import { connect } from 'react-redux';
import AffiliateComponent from './AffiliateComponent';
import { openAffiliateModal } from '../../redux/actions/affiliateActions';
import { showSnack } from '../../redux/actions/snackbarActions';
import path from '../../helpers/path';

const mapStateToProps = state => {
  return {
    path: path,
    affiliate: state.affiliate
  };
};

const mapDispatchToProps = dispatch => ({
  openAffiliateModal: () => dispatch(openAffiliateModal()),
  showSnack: text =>
    dispatch(showSnack({ id: 'affiliateCopyTage', text, type: 'success' }))
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(AffiliateComponent);



