import { connect } from 'react-redux';
import { reduxForm } from 'redux-form';
import AffiliateModal from './AffiliateModal';
import modalDecorator from '../../hocs/modalDecorator';
import { MODAL_TYPES } from '../../redux/actions/modalActions';
import {
  getAffiliate,
  updateAffiliate
} from '../../redux/actions/affiliateActions';

const mapStateToProps = state => {
  const { affiliate } = state;

  return {
    initialValues: {
      tag: affiliate.affiliateTag
    }
  };
};

const mapDispatchToProps = { getAffiliate };

const Form = reduxForm({
  form: 'updateAffiliate',
  onSubmit: (payload, dispatch) => dispatch(updateAffiliate(payload))
})(AffiliateModal);

const container = connect(
  mapStateToProps,
  mapDispatchToProps
)(Form);

export default modalDecorator({
  name: MODAL_TYPES.NEW_AFFILIATE_KEY
})(container);



