import React from 'react';
import PropTypes from 'prop-types';
import APButton from '../common/APButton';

import './AffiliateComponent.css';
import copyToClipboard from '../../helpers/clipboardHelper';
import { getBEMClasses } from '../../helpers/cssClassesHelper';
import config from '../../config/';
import AffiliateModalContainer from './AffiliateModalContainer';

const affiliateClasses = getBEMClasses('affiliate');

const AffiliateComponent = (props, context) => {
  const { path, affiliate, showSnack, openAffiliateModal } = props;

  const affiliateTag = `${window.location.origin}${path('')}?aff=${
    affiliate.affiliateTag
  }`;

  const getModalButtonText = () => {
    return affiliate.affiliateTag ? context.t('Edit') : context.t('Create');
  };

  return (
    <section className={affiliateClasses()}>
      <header className={affiliateClasses('main-header')}>
        <h1 className={affiliateClasses('header')}>
          {context.t('Join the {n} Affiliate Program', {
            n: config.global.siteName
          })}
        </h1>
        <p className={affiliateClasses('description')}>
          {context.t(
            'Our affiliate program lets you earn a bonus for each successful referral registered to your account. Create an affiliate link below, copy it and email to a friend. Your friends can click on the affiliate link and sign up for an account, at which point you will see the number of affliates increase on this page.'
          )}
        </p>
      </header>
      <section className={affiliateClasses('body')}>
        <section className={affiliateClasses('container')}>
          <header className={affiliateClasses('container-header')}>
            <h1 className={affiliateClasses('container-header--title')}>
              {context.t('Current Active Tag')}
            </h1>
          </header>
          <section className={affiliateClasses('container-content')}>
            <p className={affiliateClasses('tag')}>{affiliate.affiliateTag}</p>
            {affiliate.affiliateTag && (
              <p
                id="affiliateTag"
                className={affiliateClasses('tag-description')}>
                {affiliateTag}
              </p>
            )}
            <div className={affiliateClasses('btn-container')}>
              <APButton
                customClass={affiliateClasses()}
                onClick={() => openAffiliateModal()}>
                {getModalButtonText()}
              </APButton>
              {affiliate.affiliateTag && (
                <APButton
                  customClass={affiliateClasses()}
                  onClick={() => {
                    showSnack(
                      context.t(
                        'The affiliate tag has been copied to the clipboard.'
                      )
                    );
                    copyToClipboard(affiliateTag);
                  }}>
                  {context.t('Copy')}
                </APButton>
              )}
            </div>
          </section>
        </section>
        <section className={affiliateClasses('container')}>
          <header className={affiliateClasses('container-header')}>
            <h1 className={affiliateClasses('container-header--title')}>
              {context.t('Number of Active Affiliates')}
            </h1>
          </header>
          <section className={affiliateClasses('container-content')}>
            <p className={affiliateClasses('tag')}>
              {affiliate.affiliateCount} {context.t('Affiliates')}
            </p>
          </section>
        </section>
      </section>
      <AffiliateModalContainer />
    </section>
  );
};

AffiliateComponent.contextTypes = {
  t: PropTypes.func.isRequired
};

export default AffiliateComponent;



